<template>
  <div>
    <div class="use-descript">
      <i class="fal fa-angle-up"></i>
      <span v-if="rwdStatus == false">可點擊輪播器內的文字連結查看作品相關內容</span>
      <span v-else>
        可點擊輪播器內的文字連結
        <br />查看作品相關內容
      </span>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.use-descript {
  padding: 10px 0 20px 0;
  i {
    font-size: 50px;
    animation: arrowAnimate 2s infinite;
  }
  @keyframes arrowAnimate {
    0% {
      opacity: 0;
      transform: translateY(10px);
    }
    50% {
      opacity: 1;
      transform: translateY(-10px);
    }
    100% {
      opacity: 0;
      transform: translateY(-10px);
    }
  }
  span {
    display: block;
    font-size: 20px;
  }
  @media screen and (max-width: 414px) {
    span {
      padding: 0 20px 0 20px;
    }
  }
}
</style>
<script>
export default {
  data() {
    return {
      rwdStatus: false,
    };
  },
  created() {
    window.innerWidth == 414
      ? (this.rwdStatus = true)
      : (this.rwdStatus = false);
  },
};
</script>