    }
  }
  .go-top-active {
    opacity: 1;
    transform: translate(-85%, -50%) scale(1);
  }
  @media screen and (max-width: 414px) {
    .go-top-active {
      transform: translate(-50%, -50%) scale(1);
    }
  }
}
.nav-rwd-menu {
  position: fixed;
  top: 4%;
  left: 0;
  transform: translate(100%, 15%);
  z-index: 12;
  transition: 0.7s ease;
  .line {
    height: 3px;
    width: 30px;
    background-color: white;
    margin: 5px 0 5px 0;
    transition: 1s ease;
  }
  .line:nth-of-type(1) {
    transform: translateY(0px) rotate(0deg);
  }
  .line:nth-of-type(2) {
    opacity: 1;
    transform: scale(1);
  }
  .line:nth-of-type(3) {
    transform: translateY(0px) rotate(0deg);
  }
  .line-trans-FT {
    transform: translateY(8px) rotate(135deg) !important;
  }
  .line-trans-SD {
    opacity: 0 !important;
    transform: scale(0.1) !important;
  }
  .line-trans-TD {
    transform: translateY(-8px) rotate(-135deg) !important;
  }
}
.nav-rwd-menu-hide {
  top: -100%;
}
.nav-rwd-outer {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 11;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.7);
  transition: 1s ease;
  .nav-rwd {
    position: absolute;
    top: 0;
    bottom: 0;
    color: white;
    background: linear-gradient(
      0deg,
      rgb(90, 90, 90),
      rgb(30, 30, 30),
      rgb(0, 0, 0)
    );
    transform: translate(-160px);
    transition: 1s ease;
    z-index: 12;
    ul {
      list-style: none;
      padding: 45px 85px 0 85px;
      text-align: center;
      line-height: 8.5em;
      font-size: 20px;
    }
  }
  .nav-rwd-active {
    transform: translateX(0px);
  }
}
.nav-rwd-outer-active {
  opacity: 1;
}
.resume-all-blur {
  filter: blur(3px);
}
</style>
<script>
import video from "../assets/banner.mp4";
import $ from "jquery";
export default {
