            "各項屬性的運用，如排版上的結構變化、製作動畫延遲效果、顏色漸層 DOM 元素陰影、持續性的動畫變型等設置。",
        },
        {
          contentTitle: "Scss",
          contentInSide:
            "CSS3 的預處理器運用，結構上的撰寫，有效提高 CSS3 代碼的可讀性與撰寫性，並方便管理樣是內容，建立 Mixin 專屬個人化的屬性設置，並於專案中套用所設置的屬性內容。",
        },
        {
