        {
          contentTitle: "Javascript",
          contentInSide:
            "各項語法撰寫，包含各類型轉換，陣列與物件的運用，在資料量大時製作物件資料管理資料，並透過迴圈渲染大量物件資料，同樣透過物件資料製作 NodeList 元素動畫，透過 Function 傳值傳址與 Function Scoped 的了解，三元運算式運用，還有 input 內容的正則驗證與各項功能開關、動畫設計，製作函式工廠、閉包等、或透過 reture 將傳入運算函式中的內容，轉寫回傳內容或格式，以及各瀏覽器相容性加載原型屬性使用、讓無法支援 ES6 的部分方法只支援 ES5 的瀏覽器也可同樣支援使用、平時都是使用 ES6 撰寫。",
        },
        {
          contentTitle: "ES6",
          contentInSide:
            "了解 ES6 寫法，如陣列與物件的解構，透過解構的方式重新組合物件，配合網頁元素使用，透過 fetch 做 API 串接再透過 json() 轉型資料，ForEach、Filter、Reduce 等和箭頭函式的運用，更有效的撰寫程是語言，並提高語言的可讀性。",
        },
        {
          contentTitle: "AJAX",
          contentInSide:
            "串接各項 API 資料，製作網頁內容，並透過 API 即時更新網頁內容，目前有使用過的 API 如高雄旅遊網、中央氣象局資料、Google Youtube API，還有自建的 FireBase 資料庫。",
        },
        {
          contentTitle: "Node Js",
          contentInSide:
            "基本的 npm 使用，使用 require 加載模組，並透過 module.exports 載入不同模組內容使用，目前有使用 express 建立 Restful Api 並透過中介軟體 body-parser 將前端取回的資料內容轉換成 JSON 格式，將資料寫進 SQL 資料庫",
        },
        {
          contentTitle: "Sql Server",
          contentInSide:
            "基礎的 Sql 語法與 CRUD、JOIN、LIKE 語法運用，還有表單的關聯性建立與正規化、SQL Injection 的防範撰寫方式、寫入欄位的型別認知，如 int、varchar、nvarchar、datetime 等不同的格式寫入方法，目前有實做在電商網頁中。",
        },
        {
          contentTitle: "C#",
          contentInSide:
            "基礎強型別與集合和陣列的認知與差別，集合轉換成物件的使用，public 與 private 差異，與 static 靜態參數與非靜態參數的使用，曾使用過 ASP.Net MVC 撰寫過後 Controller 與 Model 的應用邏輯並串接 Sql Server 資料庫。",
        },
        {
          contentTitle: "JsonServer & FireBase",
          contentInSide:
            "閱讀 JSON API 文件，各項資料的解讀與運用，使用 JsonServer、FireBase 製作 JSON 格式資料，並在專案中使用虛擬資料庫作串接與功能測試，在實際上線網頁使用線上資料庫。",
        },
        {
          contentTitle: "RWD",
          contentInSide:
            "製作響應式板面內容與不同手機畫面大小與跨瀏覽器的相容性，如開關式動畫選單，Table 元素的橫向卷軸等。",
        },
        {
          contentTitle: "Bootstrap 4",
          contentInSide:
            "使用格線系統 ( gride system ) 有效控制版面在各分辨率時的版面設置，與各項原件的套用，如 Modal、Carousol、Pagination 等。",
        },
        {
          contentTitle: "Vue & VueCli",
          contentInSide:
            "VUE 元件製作與套件、模板語法運用，運用 params 在分頁間傳遞參數，透過 props 與 emit 在元件間傳遞字串與物件，npm 的安裝運用，使用的套件如 RouterLink、Axios、Boostrap、Md5、V-charts、express、body-parser、cors、morgan、mssql、openCC 等，透過 Axios 的 get、post、patch、delete 串接 Json server 虛擬資料庫與 FireBase 線上資料庫與 Sql Server 資料庫的內容做畫面的即時內容更新，使用 SPA 撰寫單頁切換功能，再透過 WebPack 將 Vue 元件打包成網頁，目前有實作在電商網站上、個人履歷、部落格、記帳管理系統。",
        },
        {
          contentTitle: "Github",
          contentInSide:
            "一般的使用與操作，並將平時製作完成的網頁推送上平台，目前的推送以 Master 主線為主，Branch 分支的部分較少使用，但了解主線與分支的推送原理。",
        },
        {
          contentTitle: "Docker",
          contentInSide:
            "基本操做 docker pull、rm等，透過 Docker 建立本地端與容器間的映射連結 Server，並透過 Node Js 介接由 Docker 建立的 Sql Server 虛擬機 Server。",
        },
      ],
    };
  },
  mounted() {
    document
      .querySelectorAll(".imgs")
      .forEach((key, index) => key.setAttribute("data-num", (index += 1)));
    document
      .querySelectorAll(".nav-option")
      .forEach((key, index) => key.setAttribute("data-num", (index += 1)));
    this.imgSwiper();
  },
  methods: {
    scrollContentPart() {
      const downArrow = document.querySelector(".down-arrow");
      const mainSkillOuter = document.querySelector(".main-skill-outer");
      const collectionOuter = document.querySelector(".collection-outer");
      const hobbyOuter = document.querySelector(".hobby-outer");
      const selfOuter = document.querySelector(".self-outer");
      const screenScrollY = window.scrollY + window.innerHeight;
      const arrowPos = downArrow.offsetTop + downArrow.offsetHeight / 4;
      const skillPos =
        mainSkillOuter.offsetTop + mainSkillOuter.offsetHeight / 4;
      const collectionPos =
        collectionOuter.offsetTop + collectionOuter.offsetHeight / 4;
      const hobbyPos = hobbyOuter.offsetTop + hobbyOuter.offsetHeight / 4;
      const selfPos = selfOuter.offsetTop + selfOuter.offsetHeight / 4;
      this.scrollContent(
        screenScrollY,
        arrowPos,
        skillPos,
        collectionPos,
        hobbyPos,
        selfPos
      );
    },
    scrollItems() {
      if (window.scrollY < this.scrollTemp) {
        this.rwdTitleAnimate = false;
        this.rwdIconOptionsAnimate = false;
      } else {
        this.rwdTitleAnimate = true;
        this.rwdIconAnimate == true
          ? (this.rwdIconOptionsAnimate = false)
          : (this.rwdIconOptionsAnimate = true);
      }
      this.scrollTemp = window.scrollY;
      window.scrollY >= 200
        ? (this.goTopAnimate = true)
        : (this.goTopAnimate = false);
    },
    scrollContent(
      screenScrollY,
      arrowPos,
      skillPos,
      collectionPos,
      hobbyPos,
      selfPos
    ) {
      this.scrollItems();
      arrowPos < screenScrollY
        ? (this.arrowAnimate = true)
        : (this.arrowAnimate = false);

      if (skillPos < screenScrollY) {
        this.mainSkillOuterAnimate = true;
        setTimeout(() => (this.mainSkillAnimate = true), 700);
      } else {
        this.mainSkillOuterAnimate = false;
        this.mainSkillAnimate = false;
      }

      if (collectionPos < screenScrollY) {
        this.collectionOuterAnimate = true;
        setTimeout(() => (this.collectionAnimate = true), 700);
      } else {
        this.collectionOuterAnimate = false;
        this.collectionAnimate = false;
      }

      if (hobbyPos < screenScrollY) {
        this.hobbyOuterAnimate = true;
        setTimeout(() => (this.hobbyAnimate = true), 700);
      } else {
        this.hobbyOuterAnimate = false;
        this.hobbyAnimate = false;
      }

      if (selfPos < screenScrollY) {
        this.selfOuterAnimate = true;
        setTimeout(() => (this.selfAnimate = true), 700);
        setTimeout(() => (this.selfDetailsAnimate = true), 1001);
      } else {
        this.selfOuterAnimate = false;
        this.selfAnimate = false;
        this.selfDetailsAnimate = false;
      }
    },
    changeCollection() {
      document
        .querySelector(".collection-description")
        .classList.add("collection-description-light");
      setTimeout(
        () =>
          document
            .querySelector(".collection-description")
            .classList.remove("collection-description-light"),
        500
      );
      document
        .querySelector(".collection-show")
        .classList.add("collection-show-hide");
      setTimeout(() => this.$router.push(`${this.collectUrl}`, () => {}), 1001);
      setTimeout(
        () =>
          document
            .querySelector(".collection-show")
            .classList.remove("collection-show-hide"),
        1001
      );
    },
    showNavBg(num) {
      let navBody = document
        .querySelector(".nav-option")
        .getBoundingClientRect();
      document.querySelector(".nav-bg").style.display = "block";
      setTimeout(
        () => document.querySelector(".nav-bg").classList.add("nav-bg-active"),
        100
      );
      document.querySelector(".nav-bg").style.top = `${
        window.scrollY == 0 ? navBody.top : navBody.top + window.scrollY
      }px`;
      document.querySelector(".nav-bg").style.left = `${
        num == 0 ? navBody.left : navBody.width * num
      }px`;
      document.querySelector(".nav-bg").style.width = `${navBody.width}px`;
      document.querySelector(".nav-bg").style.height = `${navBody.height}px`;
    },
    hideNavBg() {
      document.querySelector(".nav-bg").classList.remove("nav-bg-active");
      setTimeout(
        () => (document.querySelector(".nav-bg").style.display = ""),
        500
      );
    },
    goTop() {
      $("html, body").animate({ scrollTop: 0 }, 2500);
    },
    goToTitle(title) {
      this.goToTitleName.forEach((key) => {
        if (key.titleNum == title) {
          $("html, body").animate(
            { scrollTop: $(`${key.titleName}`)[0].offsetTop },
            3000
          );
        }
      });
      if (this.rwdStatus == true) {
        this.showRwdMenu();
        return;
      }
      document
        .querySelectorAll(".nav-option")
        .forEach((key) =>
          Number(key.dataset.num) == title
            ? key.classList.add("nav-option-active")
            : key.classList.remove("nav-option-active")
        );
      this.hideNavBg();
      setTimeout(() => this.showNavBg(), 500);
    },
    showRwdMenu() {
      if (this.rwdIconAnimate == false) {
        this.rwdIconAnimate = true;
        document.querySelector(".nav-rwd-outer").style.display = "block";
        setTimeout(() => (this.rwdMenuAnimate = true), 100);
      } else {
        this.rwdMenuAnimate = false;
        this.rwdIconAnimate = false;
        this.rwdTitleAnimate == false
          ? (this.rwdIconOptionsAnimate = false)
          : (this.rwdIconOptionsAnimate = true);
        setTimeout(
          () => (document.querySelector(".nav-rwd-outer").style.display = ""),
          901
        );
      }
    },
    showSkill(num) {
      let pos = this.skillNumTemp.findIndex((key) => key == num);
      if (pos == -1) {
        this.skillNumTemp.push(num);
        [...document.querySelectorAll(".skill-card-body")][num].classList.add(
          "skill-card-body-show"
        );
        [...document.querySelectorAll(".skill-card-body-outer")][
          num
        ].classList.add("skill-card-body-outer-toggle");
      } else {
        this.skillNumTemp.forEach((key) => {
          if (key == num) {
            [...document.querySelectorAll(".skill-card-body-outer")][
              key
            ].classList.remove("skill-card-body-outer-toggle"),
              [...document.querySelectorAll(".skill-card-body")][
                key
              ].classList.remove("skill-card-body-show");
          }
        });
        this.skillNumTemp.splice(pos, 1);
      }
    },
    autoDecription() {
      document
        .querySelector(".collection-description")
        .classList.remove("descrip-active");
      this.swiperDescriptionCounter = setTimeout(
        () =>
          document
            .querySelector(".collection-description")
            .classList.add("descrip-active"),
        5000
      );
    },
    imgCounterPart() {
      document
        .querySelectorAll(".imgs")
        .forEach((key) =>
          Number(key.dataset.num) == this.swiperCount
            ? key.classList.add("img-active")
            : key.classList.remove("img-active")
        );
      this.collectionDescription.forEach((key) => {
        if (key.collectNum == this.swiperCount) {
          this.descrip = key.collectName;
          this.collectUrl = key.collectRouter;
        }
      });
    },
    imgCounter() {
      switch (this.handCount) {
        case true:
          document
            .querySelector(".collection-description")
            .classList.remove("descrip-active");
          clearTimeout(this.swiperDescriptionCounter);
          this.imgCounterPart();
          break;
        case false:
          this.swiperCount == 15
            ? (this.swiperCount = 1)
            : (this.swiperCount += 1);
          this.imgCounterPart();
          this.autoDecription();
          break;
      }
    },
    imgSwiper() {
      switch (this.swiperSwitch) {
        case true:
          this.handCount = true;
          this.imgCounter();
          clearInterval(this.swiperCounter);
          clearTimeout(this.swiperDelayCounter);
          this.swiperDelayCounter = setTimeout(() => {
            this.swiperSwitch = false;
            this.imgSwiper();
          }, 5000);
          break;
        case false:
          this.swiperCounter = setInterval(() => {
            this.handCount = false;
            this.imgCounter();
          }, 6000);
          break;
      }
    },
    prevImg() {
      this.swiperCount == 1 ? (this.swiperCount = 15) : (this.swiperCount -= 1);
      this.handCount = true;
      this.swiperSwitch = true;
      this.imgSwiper();
    },
    nextImg() {
      this.swiperCount == 15 ? (this.swiperCount = 1) : (this.swiperCount += 1);
      this.handCount = true;
      this.swiperSwitch = true;
      this.imgSwiper();
    },
  },
  created() {
    this.$router.push("/descript", () => {});
    window.addEventListener("scroll", this.scrollContentPart);
    window.innerWidth <= 1024
      ? (this.rwdStatus = true)
      : (this.rwdStatus = false);
  },
  destroyed() {
    window.removeEventListener("scroll", this.scrollContentPart);
  },
};
</script>